body{
  margin: 0 auto;
  font-family:Inter;
  color:#2B283A;
  box-sizing: border-box;
}
.header{
  
  background-color:#F55A5A;
  display:flex;
  justify-content:center;
}
.header-image{
  height:24px;
  width:24px;
  margin-top:16px;
  padding-right: 7px;
}

.header-title{
  margin-top:20px;
  font-weight: medium;
  color:white;
  font-size: 14px;
} 

/* Journal */

.journal{
  justify-content:center;
  margin:45px 40px 36px 80px;
  font-size:15px;
  border-bottom: 1px solid #e2dfdf;
  display:flex;
  overflow:ellipsis;
 
}
.journal-image{
  margin-top:36px;
  height: 230px;
  width:250px;
  float:left;
  border-radius:5px;
  margin-right:19px;
  margin-bottom:36px;
}

.location{
  padding-top:25px;
  margin-bottom:4px;
  padding-right:12px;
  padding-left:4px;
  
}
.journal-maps{
  color:#918E9B;
  margin-left:12px;
  font-family: Inter;

}
.locationIcon{
  height:10px;
  width:8px;
  margin-right:10px;
}
.journal-title{
  margin-top:4px;
  font: 25px Bold;
  letter-spacing:17%;
}
.journal-date{
  margin-top:14px;
  margin-bottom:4px;
}
.journal-description{
  line-height: 150%;
  font: 17px Regular;
  text-align: justify;
  width: 90%;
  height: 60px;
}


.journal-location{
  display:flex;
  margin-right:40px;
  padding-block:10px;
  flex-direction:column;
  word-wrap: break-word;
}
.top-section{
  display:flex;
  flex-direction:row;
  font: 15px Regular;

}

